import { Link } from "gatsby"
import React from "react"
import Img from "gatsby-image"


const Header = (props) => {
  const siteMetadata= props.siteMetadata
  const logo = props.logo
  return (
      <div className={'header'}>
        <div className={'homecontainer'}><Link to="/">
          <Img className={'logo'} alt={siteMetadata.title} fluid={logo}/></Link>
        </div>
        <div className={'navbarcontainer'}>
        <div key="contact-form">
          <Link to="mailTo:antoineroy.dev@gmail.com">Contact</Link>
        </div> 
        </div>
      </div>
    )
}

export default Header

